/* add css styles here (optional) */

.float {
  position: fixed;
  top: 0;
  right: -450px;
  width: 450px;
  height: 100%;
  background-color: #fff;
  box-sizing: border-box;
  transition: right 0.2s;
  z-index: 9999999;
}

.float-open {
  right: 0 !important;
  width: 82%;
}

.float-close-btn {
  width: 50px;
  height: 50px;
  color: #ececec;
  background-color: #f6406f;
  text-align: center;
  line-height: 50px;
  position: absolute;
  top: 2%;
  left: -50px;
  cursor: pointer;
}

.bag {
  width: 40px;
  height: 40px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-right: 15px;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.bag--float-cart-closed {
  position: relative;
  background-color: #f6406f;
  background-size: 50%;
  left: -70px;
  width: 60px;
  height: 60px;
  cursor: pointer;
  top: -4px;
}

@media (max-width: 450px) {
  .bag--float-cart-closed {
    left: -70px;
  }
  .float-cart__header {
    box-sizing: border-box;
    text-align: center;
    padding: 30px 0 !important;
  }
  .float-cart__footer {
    height: 144px !important;
  }

  .sub-totals {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .sub-price {
    font-size: 1.2em;
  }

  .sub-quantity {
    font-size: 1.2em;
  }
  .bag__quantity-expanded {
    left: 180%;
    top: 80%;
  }

  .shelf-item--quantity-buttons {
    position: absolute;
    top: 50%;
    left: 40%;
    display: flex;
    flex-direction: row;
  }
  .shelf-item--quantity-buttons .increase {
    margin-left: -10%;
  }
  .shelf-item--quantity-buttons input {
    display: none;
  }
  .shelf-item--quantity-buttons .input-field {
    font-size: 1.3em;
    display: none;
  }
  .shelf-item--quantity-buttons button {
    background-color: transparent;
  }

  .item--card-body--quantity-buttons .input-field .label-icon {
    font-size: 1.3em;
    display: none;
  }
}
.bag__quantity,
.bag__quantity-expanded {
  display: inline-block;
  width: 18px;
  height: 18px;
  color: #0c0b10;
  font-weight: bold;
  font-size: 0.7em;
  text-align: center;
  line-height: 18px;
  border-radius: 50%;
  background-color: #eabf00;
}
.bag__quantity {
  position: relative;
  bottom: -35px;
  right: -5px;
}
.bag__quantity-expanded {
  position: relative;
  bottom: -30px;
  right: -45px;
  height: 1.85em;
  width: 2em;
  font-size: 1.2em;
  padding-top: 6px;
}
.float-cart__header {
  box-sizing: border-box;
  text-align: center;
  padding: 45px 0;
}

.header-title {
  font-weight: bold;
  font-size: 2em;
  vertical-align: middle;
  position: relative;
  left: -8%;
  bottom: -5%;
}

.float-cart__shelf-container {
  position: relative;
  min-height: 280px;
  padding-bottom: 200px;
}

.shelf-empty {
  text-align: center;
  line-height: 40px;
  font-size: 1.2em;
}
.shelf-empty div {
  text-decoration: underline;
  color: #039be5;
}
.shelf-item {
  position: relative;
  box-sizing: border-box;
  padding: 5%;

  transition: background-color 0.2s, opacity 0.2s;
}

.shelf-item::before {
  content: "";
  width: 90%;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 0;
  left: 5%;
}

.shelf-item__del {
  width: 20px;
  height: 20px;
  top: 15px;
  right: 5%;
  border-radius: 50%;
  position: absolute;
  background-size: auto 100%;
  background-image: url(data:image/svg+xml;utf8;base64,);
  background-repeat: no-repeat;
  z-index: 2;
  cursor: pointer;
}

.shelf-item__del:hover {
  background-position-x: -17px;
}

.shelf-item__thumb,
.shelf-item__details,
.shelf-item__price {
  display: inline-block;
  vertical-align: middle;
  font-size: 1.4em;
}

.shelf-item__thumb {
  vertical-align: middle;
  width: 15%;
  margin-right: 3%;
}

.shelf-item__thumb img {
  width: 100%;
  height: auto;
}

.shelf-item__details {
  width: 57%;
}

.shelf-item__details .title {
  margin: 0;
  text-transform: capitalize;
}

.shelf-item__details .desc {
  margin: 0;
}

.shelf-item__price {
  color: #eabf00;
  text-align: right;
  width: 25%;
  margin-left: -8%;
}

.float-cart__footer {
  box-sizing: border-box;
  padding: 5%;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 200px;
  z-index: 2;
  background-color: #fff;
}

.continue-btn {
  color: #ececec;
  text-transform: uppercase;
  background-color: #f6406f !important;
  text-align: center;
  padding: 2% 2% 5.5% 2% !important;
  margin-top: 20px;
  cursor: pointer;
  color: white !important;
  transition: background-color 0.2s;
  width: 20em;
  height: 2em;
  font-size: 1.1em !important;
}

.sub {
  text-transform: uppercase;
  font-size: 1.4em;
  font-weight: 700;
}

@media screen and (min-width: 640px) {
  .float-cart__content::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 10px;
    padding: 10px;
  }
  .float-cart__content::-webkit-scrollbar-thumb {
    border-radius: 4px;
  }
  .shelf-item--quantity-buttons {
    position: absolute;
    top: 50%;
    left: 33%;
    display: flex;
    flex-direction: row;
  }
  .shelf-item--quantity-buttons input {
    display: none;
  }
  .shelf-item--quantity-buttons .input-field {
    font-size: 1.3em;
    display: none;
  }
  .shelf-item--quantity-buttons button {
    background-color: transparent;
  }

  .float-cart__footer .continue-btn {
    width: 20%;
    margin-left: 40%;
    margin-top: -10%;
    padding-bottom: 4.5% !important;
  }
  .sub {
    font-size: 1.6em;
  }
  .sub-price {
    font-size: 1.4em;
    margin-top: -3%;
    margin-left: 20%;
  }
  .sub-quantity {
    margin-top: -10px;
  }
}

.float-cart__content {
  height: 100%;
  overflow-y: scroll;
  height: 100%;
}

.float-cart__content,
.float-cart__footer {
  border-left: 0.5px solid rgba(0, 0, 0, 0.3);
}
